<template>
  <div class="ol-layer-custom" title="图层" @click="toggle"></div>
  <div id="mask" v-show="isShow" @click="toggle"></div>
  <div
    id="LayerPanel"
    :class="[isShow ? 'LayerPanel-show' : 'LayerPanel-hide']"
  ></div>
</template>

<script>
import { ref } from "vue";

export default {
  name: "LayerPanel",
  setup() {
    let isShow = ref(false);
    function toggle() {
      isShow.value = !isShow.value;
    }
    return { isShow, toggle };
  },
};
</script>

<style scoped>
.ol-layer-custom {
  position: absolute;
  border-radius: 2px 2px 0 0;
  top: 0.5em;
  right: 0.5em;
  margin: 1px;
  padding: 0;
  color: var(--ol-subtle-foreground-color);
  font-weight: bold;
  text-decoration: none;
  font-size: inherit;
  text-align: center;
  height: 2em;
  width: 2em;
  line-height: 0.4em;
  background-color: var(--ol-background-color);
  border: none;
  border-radius: 2px;
  background-image: url(../../assets/图层.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 80% 80%;
}

#mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

#LayerPanel {
  position: absolute;
  height: 100%;
  width: 70%;
  background-color: white;
  border-radius: 1.5px;
  transition: right 0.6s ease;
}

.LayerPanel-show {
  right: 0;
}

.LayerPanel-hide {
  right: -100%;
}
</style>
