<template>
  <div id="map"></div>
  <div id="panel">
    坐标系：{{ proj }}<br />
    坐标：{{ locationCoord }}<br />准心：{{ crosshairPoi }}<br />精度：{{
      accuracy
    }}<br />海拔：{{ altitude }}<br />方位角：{{ heading }}
  </div>
  <div id="crosshair">✛</div>
  <keep-alive><LayerPanel /></keep-alive>
</template>

<script>
import { onMounted, ref } from "vue";
import "ol/ol.css";
import { Map, View } from "ol";
import { Tile as TileLayer, Graticule, Vector as VectorLayer } from "ol/layer";
import { BingMaps } from "ol/source";
import {
  defaults as defaultControls,
  Zoom as ZoomControl,
  ScaleLine,
  MousePosition,
} from "ol/control";
import { defaults as defaultInteractions } from "ol/interaction";
import { toStringHDMS } from "ol/coordinate";
import ZoomExtent from "../../components/ZoomExtent";
import GridControl from "../../components/GridControl";
import LocationControl from "../../components/LocationControl";
import LayerPanel from "../../components/LayerPanel";
import Geolocation from "ol/Geolocation";
import Feature from "ol/Feature";
import { Point } from "ol/geom";
import { Style, Stroke, Fill, Circle, RegularShape } from "ol/style";
import { Vector as VectorSource } from "ol/source";
import vConsole from "vconsole";

export default {
  name: "Homepage",
  components: { LayerPanel },
  setup() {
    // let vconsole = new vConsole();
    let bingAerial = new BingMaps({
      key: "Q57tupj2UBsQNQdju4xL~xBceblfTd6icjljunbuaCw~AhwA-whmGMsfIpVhslZyknWhFYq-GvWJZqBnqV8Zq1uRlI5YM_qr7_hxvdgnU7nH",
      imagerySet: "Aerial",
    });
    const graticuleLayer = new Graticule({
      strokeStyle: new Stroke({
        color: "rgba(191,182,182,0.5)",
        width: 2,
        lineDash: [0.5, 4],
      }),
      showLabels: true,
    });
    const locationLayer = new VectorLayer();
    let map = new Map({
      controls: defaultControls({
        rotate: false,
        zoom: false,
        attribution: false,
      }),
      interactions: defaultInteractions({
        pinchRotate: false,
      }),
      layers: [
        new TileLayer({ source: bingAerial }),
        graticuleLayer,
        locationLayer,
      ],
      view: new View({
        center: [0, 0],
        zoom: 4,
        minZoom: 4,
        maxZoom: 19,
        projection: "EPSG:4326",
      }),
    });
    let proj = ref("");
    let locationCoord = ref("");
    let accuracy = ref("");
    let altitude = ref("");
    let heading = ref("");
    let crosshairPoi = ref("");

    onMounted(() => {
      map.setTarget("map");
      proj.value = map.getView().getProjection().getCode();
      map.addControl(
        new ZoomControl({
          zoomInTipLabel: "放大",
          zoomOutTipLabel: "缩小",
        })
      );
      map.addControl(new ScaleLine());
      map.addControl(
        new MousePosition({
          className: "ol-mousePosition-custom",
          projection: map.getView().getProjection(),
          placeholder: "未知",
          coordinateFormat: (coord) => {
            return toStringHDMS(coord);
          },
        })
      );
      map.addControl(
        new ZoomExtent({
          title: "复位",
        })
      );
      map.addControl(
        new GridControl({
          title: "经纬网",
          layer: graticuleLayer,
        })
      );
      crosshairPoi.value = toStringHDMS(map.getView().getCenter(), 4);
      const geolocation = new Geolocation({
        projection: map.getView().getProjection(),
        tracking: true,
        trackingOptions: {
          enableHighAccuracy: true,
        },
      });
      let positionFeature = new Feature();
      let headingRegularShape = new RegularShape({
        fill: new Fill({ color: "#3399CC" }),
        points: 3,
        radius: 5,
        displacement: [0, 0],
        rotation: 0,
        angle: 0,
      });
      positionFeature.setStyle([
        new Style({
          image: new Circle({
            radius: 6,
            fill: new Fill({
              color: "#3399CC",
            }),
            stroke: new Stroke({
              color: "#fff",
              width: 2,
            }),
          }),
        }),
        new Style({
          image: headingRegularShape,
        }),
      ]);
      let accuracyFeature = new Feature();
      locationLayer.setSource(
        new VectorSource({
          features: [accuracyFeature, positionFeature],
        })
      );
      map.once("loadend", function () {
        let view = map.getView();
        let poi = geolocation.getPosition();
        if (poi !== undefined) {
          view.setCenter(poi);
          view.setZoom(15);
          view.setProperties({ defaultCenter: poi });
        }
      });
      geolocation.on("change", function () {
        let coordinates = geolocation.getPosition();
        locationCoord.value = toStringHDMS(coordinates, 4);
        let altitudeVlaue = geolocation.getAltitude();
        if (altitudeVlaue !== undefined) {
          altitude.value = `${altitudeVlaue.toString()}m`;
        } else {
          altitude.value = "";
        }
        positionFeature.setGeometry(
          coordinates ? new Point(coordinates) : null
        );
      });
      geolocation.on("change:accuracyGeometry", function () {
        accuracyFeature.setGeometry(geolocation.getAccuracyGeometry());
        accuracy.value = `${geolocation.getAccuracy().toString()}m`;
      });
      map.getView().on("change:center", function () {
        crosshairPoi.value = toStringHDMS(this.getCenter(), 4);
      });
      window.addEventListener("deviceorientationabsolute", function (event) {
        let alpha = event.webkitCompassHeading || event.alpha;
        headingRegularShape.setRotation((Math.PI / 180) * (360 - alpha));
        if (alpha === null || event.absolute === false) {
          headingRegularShape.setDisplacement([0, 0]);
          heading.value = "";
        } else {
          headingRegularShape.setDisplacement([0, 10]);
          heading.value = `${360 - alpha}°`;
        }
        positionFeature.changed();
      });
      map.addControl(
        new LocationControl({
          title: "定位",
          geolocation,
        })
      );
    });
    return { proj, locationCoord, accuracy, altitude, heading, crosshairPoi };
  },
};
</script>

<style scoped>
#map {
  position: absolute;
  width: 100%;
  height: 100%;
}

#panel {
  position: absolute;
  box-sizing: border-box;
  padding: 5px;
  bottom: 35px;
  will-change: contents, width;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  color: white;
  text-shadow: 1.5px 1.5px black;
}

#crosshair {
  display: inline-block;
  position: absolute;
  color: white;
  width: 50px;
  height: 50px;
  top: 50%;
  left: 50%;
  font-size: 24px;
  transform: translate(-50%, -50%);
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  -webkit-text-stroke: 0.5px black;
}
</style>

<style>
.ol-mousePosition-custom {
  bottom: 8px;
  right: 8px;
  margin: 1px;
  padding: 2px;
  border-radius: 4px;
  position: absolute;
  color: var(--ol-foreground-color);
  font-size: 12px;
  text-align: center;
  will-change: contents, width;
  background: var(--ol-partial-background-color);
  transition: all 0.25s;
}
</style>
